    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default {  
		data() {
			return {
				datalist:[],
				loadingText: '正在加载...',
				hasmore:true,
				curpage:1,
				storeInfo:[]
			}				
		},
        computed: mapState(['sellerHasLogin']),  
		onLoad() {
			 console.log('页面加载');
			this.loadData();
		},
		onShow() {
             console.log('页面显示..');
         },
         onReady(){
             console.log('页面初次显示');
         },
         onHide() {
             console.log('页面隐藏');
         },
         onUnload() {
             console.log('页面卸载');
         },
         onBackPress(){
             console.log('页面返回...');
         },
         onShareAppMessage() {
             console.log('分享!');
         },
         onReachBottom() {
             console.log('下拉加载...');
             console.log('正在加载中...');
			 this.curpage++;
			 this.loadData();
         },
         onPageScroll(){
             console.log('页面滚动...');
         },
         onPullDownRefresh() {
             console.log('上拉刷新...');
             uni.stopPullDownRefresh();
         },
        methods: {   
            async loadData() {  
				console.log('===sellerHasLogin==='+this.sellerHasLogin);
				
                if (!this.sellerHasLogin) {  
					uni.navigateTo({
						url:'login',
					})
                }else{
					if(!this.hasmore){
						this.loadingText = '暂无更多信息';
						return false;
					}
					this.$Request.post('seller_voucher/templatelist',{curpage:this.curpage}).then(res => {
						if (res.code == 200) {
							console.log(res);
							this.hasmore =res.hasmore;
							console.log(this.hasmore);
							if(!this.hasmore){
								this.loadingText = '暂无更多信息';
							}
							let _datalist = res.datas.list;
							for(var i=0;i<_datalist.length;i++){
								this.datalist.push(_datalist[i]);
							}
						}
						else{
							uni.showToast({
								icon: 'none',
								title:res.datas.error,
								duration:2000
							});
							uni.navigateTo({
								url:'login',
							});
						}
					});
				}
            },
			toPage: function(url) {
				uni.navigateTo({
					url:url,
				})
			},
			del(id){
				var _this= this;
				uni.showModal({
					title: '提示',
					content: '确认要删除吗？',
					success: function (re) {
						if (re.confirm) {
							console.log('====deleteOrder====');
							_this.$Request.post('seller_voucher/voucher_del',{id:id}).then(res => {
								if (res.code == 200) {
									console.log(res);
									let len = _this.datalist.length;
									for(let i=0;i<len;i++){
										if(id==_this.datalist[i].voucher_t_id){
											_this.datalist.splice(i, 1);
											break;
										}
									}
								}
								else{
									uni.showToast({
										icon: 'none',
										title:res.datas.error,
										duration:2000
									});
								}
							});
						} else if (re.cancel) {
							console.log('用户点击取消');
						}
					}
				});
				
				
			},
			toBack: function() {
				uni.redirectTo({
					url:'/pages/seller/seller',
				})
			}
			 
        }  
    }  
